  .monster {
    font-size: 10vmin;
    /* font-size: 0.7em; */
    /* margin: 0 auto; */
    width: 1em;
    height: 1.3em;
    border-radius: 0.5em 0.5em 0em 0em / 0.6em 0.6em 0em 0em;
    box-sizing: content-box;
    border: 0.07em solid transparent;
    position: relative;
