    display: flex;
    width: 25vw;
    height: 25vw;
    /* background-color: blue; */
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: 1s ease;
  }
  
  .backCard {
    transform: rotatey(180deg);
    transition: 1s ease;
    display: flex;
    width: 25vw;
    height: 25vw;
    /* background-color: blue; */
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }
  
  .deactive {
    display: none;
  }
  .back {
    transform: rotatey(180deg);
  }
  
  .active {
    display: block;
    transform: rotatey();
  }
  
